.sidebar img{
    height: 45px;
    cursor: pointer;
}
.sidebar{
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: center;
}
.sidebar_list{
    display: flex;
    flex-direction: column;
    gap: 20px;
    /* align-items: center; */
    opacity: 0;
    transition: 300ms;
}
.sidebar_list_active{
    opacity: 1;
}
.sidebar_list_item{
    border-radius: 50%;
    height: 26px;
    width: 26px;
    list-style: none;
}

