.note{
    padding: 25px;
    height: 280px;
    width: 260px;
    display: flex;
    flex-direction: column;
    background-color: blueviolet;
    border-radius: 30px;
}
.note_text{
    /* read about flex:1; property */
    flex:1;
    resize: none;
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 1rem;
    line-height: 1.875rem;
}
.note_footer{
    display: flex;
    justify-content: space-between;
}
.note_footer img{
    height: 25px;
    width: 22px;
    opacity: 0;
    transition: 300ms;
}
.note:hover .note_footer img{
    opacity: 1;
    cursor: pointer;
}